function getCookie(cname) {
  let name = cname + "=";
  let ca = document.cookie.split(';');
  for(let i = 0; i < ca.length; i++) {
    let c = ca[i];
    while (c.charAt(0) === ' ') {
      c = c.substring(1);
    }
    if (c.indexOf(name) === 0) {
      return c.substring(name.length, c.length);
    }
  }
  return "";
}

export function eventTrackMoe(eventName, payload) {
  const moe_id = getCookie("moe_uuid");
  if (moe_id) {
    console.log(rudderanalytics.getAnonymousId());
    rudderanalytics.setAnonymousId(moe_id);
    console.log(rudderanalytics.getAnonymousId());
  }
  
  rudderanalytics.track(eventName, payload);
}

